	border:0;
	padding: 0 100px;
	.nav-wizard{
		margin-bottom: 40px;
		box-shadow:none;
		.nav-link{
			
		}
	}
	label{
		font-size: 14px;
		font-weight: 500;
		color: #000;
	}
