       url("./Flaticon_1.woff2") format("woff2"),
       url("./Flaticon_1.woff") format("woff"),
       url("./Flaticon_1.ttf") format("truetype"),
       url("./Flaticon_1.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
